// -----------------------------------------
// FIELD TYPE

enum FieldType {
    Separator = 'separator',
    String = 'string',
    LongString = 'longString',
    Email = 'email',
    Number = 'number',
    Money = 'money',
    Percentage = 'percentage',
    List = 'list',
    Options = 'options',
    OrigemDivida = 'origemDivida',
    Relation = 'relation',
    Address = 'address',
    DoacaoPessoaInventario = 'doacaoPessoaInventario',
    InternationalAddress = 'internacionalAddress',
    Date = 'date',
    Boolean = 'boolean',
}

