import { Fragment, useEffect, useState } from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Box from '@mui/material/Box';
import { Divider } from '@mui/material';
import { FormControl, InputLabel, MenuItem, Select, Switch, FormControlLabel } from '@mui/material';
import { Grid } from '@mui/material';

export default function FormDialogEdit(props) {

  if (!props) {
    return <></>;
  }

  const title = props.title ? props.title : "";
  
  const data = props.jsonInfo.data ? JSON.parse(props.jsonInfo.data) : {};
  const dataModel = props.jsonInfo.model ? JSON.parse(props.jsonInfo.model) : {};
  const dataSaveProperty = props.jsonInfo.saveProperty ? props.jsonInfo.saveProperty : "";
  const dataSavePropertyIsArray = props.jsonInfo.savePropertyIsArray ? props.jsonInfo.savePropertyIsArray : false;
  const dataSaveId = props.jsonInfo.saveId ? props.jsonInfo.saveId : 0;
  const dataReplaces = props.jsonInfo.replaces ? JSON.parse(props.jsonInfo.replaces) : {};
  const dataRelationOptions = props.jsonInfo.relationOptions ? JSON.parse(props.jsonInfo.relationOptions) : {};
  const dataRelationProperties = props.jsonInfo.relationProperties ? JSON.parse(props.jsonInfo.relationProperties) : {};

  const [formData, setFormData] = useState(data);

  useEffect(() => {
    setFormData(data ? data : {});
  }, [props.open]);
  
  const handleSubmit = (event) => {
    event.preventDefault();

    const data = [];
    data[dataSaveProperty] = dataSavePropertyIsArray ? [formData] : formData;
    
    const isItemToDelete = false;
    props.onSave(data, dataSavePropertyIsArray, dataSaveId, isItemToDelete);
    props.onClose();
  };

  const handleChange = (field, value, subField = null) => {
    setFormData((prevData) => {
      const newData = { ...prevData };
      
      if (subField) {
        // Handle nested fields (like address)
        newData[field] = {
          ...newData[field],
          [subField]: value
        };
      } else if (field.includes('-')) {
        // Handle address fields with format "field-subfield"
        const [mainField, subFieldName] = field.split('-');
        newData[mainField] = {
          ...newData[mainField],
          [subFieldName]: value
        };
      } else {
        // Handle regular fields
        newData[field] = value;
      }
      
      return newData;
    });
  };

  const renderField = (modelField, dataRelationOptions) => {
    const value = formData[modelField.field] || '';
    switch (modelField.type) {
      case 'string':
      case 'email':
        return (
          <TextField
            required={modelField.required}
            id={modelField.field}
            label={modelField.label}
            type={modelField.type === 'email' ? 'email' : 'text'}
            value={value}
            onChange={(e) => handleChange(modelField.field, e.target.value)}
            fullWidth
          />
        );

      case 'longString':
        return (
          <TextField
            required={modelField.required}
            id={modelField.field}
            label={modelField.label}
            type={'text'}
            value={value}
            multiline={true}
            onChange={(e) => handleChange(modelField.field, e.target.value)}
            fullWidth
          />
        );

      case 'number':
        return (
          <TextField
            required={modelField.required}
            id={modelField.field}
            label={modelField.label}
            type="number"
            value={value}
            onChange={(e) => handleChange(modelField.field, e.target.value)}
            fullWidth
          />
        );

      case 'money':
        return (
          <TextField
            required={modelField.required}
            id={modelField.field}
            label={modelField.label}
            type="text"
            value={value ? new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(Number(value)) : ''}
            onChange={(e) => {
              const numericValue = e.target.value.replace(/[^0-9]/g, '');
              const floatValue = numericValue ? Number(parseInt(numericValue) / 100) : null;
              handleChange(modelField.field, floatValue);
            }}
            fullWidth
            inputProps={{
              inputMode: 'numeric',
              placeholder: 'R$ 0,00'
            }}
          />
        );

      case 'percentage':
        return (
          <TextField
            required={modelField.required}
            id={modelField.field}
            label={modelField.label}
            type="number"
            value={value}
            onChange={(e) => handleChange(modelField.field, e.target.value)}
            fullWidth
          />
        );

      case 'boolean':
        return (
          <FormControlLabel
            control={
              <Switch
                checked={Boolean(value)}
                id={modelField.field}
                onChange={(e) => handleChange(modelField.field, e.target.checked)}
              />
            }
            label={modelField.label}
          />
        );

      case 'date':
        return (
          <TextField
            id={modelField.field}
            label={modelField.label}
            value={value}
            type="date"
            onChange={(e) => handleChange(modelField.field, e.target.value)}
            sx={{ width: '100%' }}
            InputLabelProps={{
              shrink: true
            }}
            inputProps={{
              max: '9999-12-31'
            }}
          />
        );

      case 'options':
        return (
          <FormControl fullWidth required={modelField.required}>
            <InputLabel id={`${modelField.field}-label`}>{modelField.label}</InputLabel>
            <Select
              labelId={`${modelField.field}-label`}
              id={modelField.field}
              value={value}
              label={modelField.label}
              onChange={(e) => handleChange(modelField.field, e.target.value)}
            >
              {modelField.options?.map((option) => {
                let label = option.label || option;
                if (dataReplaces && typeof label === 'string') {
                  Object.entries(dataReplaces).forEach(([key, value]) => {
                    label = label.replace(key, value);
                  });
                }
                return (
                  <MenuItem key={option.value || option} value={option.value || option}>
                    {label}
                  </MenuItem>
                );
              })}
            </Select>
          </FormControl>
        );

      case 'origemDivida':
        return (
          <FormControl fullWidth required={modelField.required}>
            <InputLabel id={`${modelField.field}-label`}>{modelField.label}</InputLabel>
            <Select
              labelId={`${modelField.field}-label`}
              id={modelField.field}
              value={value}
              label={modelField.label}
              onChange={(e) => handleChange(modelField.field, e.target.value)}
            >
              {dataRelationProperties?.map((item) => {
                const value = `${item.tipo}:${item.id}`;
                return (
                  <MenuItem key={value} value={value}>
                    {item.tipo} - {item.nome}
                  </MenuItem>
                );
              })}
            </Select>
          </FormControl>
        );

      case 'relation':
        const relationValue = value || {};
        return (
          <Grid container columnSpacing={2} marginBottom={0}>
            <Grid item xs={10} md={5}>
              <FormControl fullWidth required={modelField.required}>
                <InputLabel id={`${modelField.field}-tipo-label`}>{modelField.label}</InputLabel>
                <Select
                  labelId={`${modelField.field}-tipo-label`}
                  id={`${modelField.field}-tipo`}
                  value={relationValue.tipo || ''}
                  label={modelField.label}
                  onChange={(e) => handleChange(modelField.field, { ...relationValue, tipo: e.target.value })}
                >
                  {modelField.options?.map((option) => (
                    <MenuItem key={option} value={option}>
                      {option}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={4} md={1} marginTop={2}>
              <>De</>
            </Grid>

            <Grid item xs={10} md={5}>
              <FormControl fullWidth required={modelField.required}>
                <InputLabel id={`${modelField.field}-id-label`}>Pessoa</InputLabel>
                <Select
                  labelId={`${modelField.field}-id-label`}
                  id={`${modelField.field}-id`}
                  value={relationValue.id || ''}
                  label='Pessoa'
                  onChange={(e) => handleChange(modelField.field, { ...relationValue, id: e.target.value })}
                >
                    {dataRelationOptions.map((option) => (
                      <MenuItem key={option.id} value={option.id}>
                        {option.nome}
                      </MenuItem>
                    ))}
                </Select>
              </FormControl>
            </Grid>
          </Grid>
        );

      case 'list':
        return (
          <TextField
            required={modelField.required}
            id={modelField.field}
            label={modelField.label}
            value={Array.isArray(value) ? value.join(', ') : ''}
            onChange={(e) => handleChange(modelField.field, e.target.value.split(',').map(item => item.trim()))}
            helperText="Digite valores separado por vírgula"
            fullWidth
          />
        );

      case 'address':
        const addressValue = value || {};
        return (
          <Grid container columnSpacing={2} marginBottom={0}>
            <Grid item xs={12} md={6}>
              <TextField
                required={modelField.required}
                id={`${modelField.field}-rua`}
                label="Rua"
                value={addressValue.rua || ''}
                onChange={(e) => handleChange(modelField.field, e.target.value, 'rua')}
                fullWidth
              />
              <TextField
                required={modelField.required}
                id={`${modelField.field}-cidade`}
                label="Cidade"
                value={addressValue.cidade || ''}
                onChange={(e) => handleChange(modelField.field, e.target.value, 'cidade')}
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth required={modelField.required}>
                <InputLabel id={`${modelField.field}-estado-label`}>Estado</InputLabel>
                <Select
                  labelId={`${modelField.field}-estado-label`}
                  id={`${modelField.field}-estado`}
                  value={addressValue.estado || ''}
                  label="Estado"
                  onChange={(e) => handleChange(modelField.field, e.target.value, 'estado')}
                >
                  <MenuItem value="AC">Acre</MenuItem>
                  <MenuItem value="AL">Alagoas</MenuItem>
                  <MenuItem value="AP">Amapá</MenuItem>
                  <MenuItem value="AM">Amazonas</MenuItem>
                  <MenuItem value="BA">Bahia</MenuItem>
                  <MenuItem value="CE">Ceará</MenuItem>
                  <MenuItem value="DF">Distrito Federal</MenuItem>
                  <MenuItem value="ES">Espírito Santo</MenuItem>
                  <MenuItem value="GO">Goiás</MenuItem>
                  <MenuItem value="MA">Maranhão</MenuItem>
                  <MenuItem value="MT">Mato Grosso</MenuItem>
                  <MenuItem value="MS">Mato Grosso do Sul</MenuItem>
                  <MenuItem value="MG">Minas Gerais</MenuItem>
                  <MenuItem value="PA">Pará</MenuItem>
                  <MenuItem value="PB">Paraíba</MenuItem>
                  <MenuItem value="PR">Paraná</MenuItem>
                  <MenuItem value="PE">Pernambuco</MenuItem>
                  <MenuItem value="PI">Piauí</MenuItem>
                  <MenuItem value="RJ">Rio de Janeiro</MenuItem>
                  <MenuItem value="RN">Rio Grande do Norte</MenuItem>
                  <MenuItem value="RS">Rio Grande do Sul</MenuItem>
                  <MenuItem value="RO">Rondônia</MenuItem>
                  <MenuItem value="RR">Roraima</MenuItem>
                  <MenuItem value="SC">Santa Catarina</MenuItem>
                  <MenuItem value="SP">São Paulo</MenuItem>
                  <MenuItem value="SE">Sergipe</MenuItem>
                  <MenuItem value="TO">Tocantins</MenuItem>
                </Select>
              </FormControl>
              <TextField
                required={modelField.required}
                id={`${modelField.field}-pais`}
                label="País"
                value="Brasil"
                fullWidth
                disabled
                onChange={(e) => handleChange(modelField.field, e.target.value, 'pais')}
              />
            </Grid>
          </Grid>
        );

      case 'doacaoPessoaInventario':
        const doacaoValue = Array.isArray(value) ? value : [];
        return (
          <Grid container columnSpacing={2} marginBottom={0}>
            { dataRelationOptions.filter(x => x.id != -1).map((option) => {
              const pessoaDoacaoValue = doacaoValue.find(d => d.pessoa_inventario_id === option.id) || {};
              return (
                <Fragment key={option.id}>
                  <Grid item xs={12} md={6}>
                    <TextField
                      required={modelField.required}
                      id={`${modelField.field}-pessoa-${option.id}`}
                      value={option.nome}
                      label="Quanto foi doado para:"
                      type="text"
                      disabled
                      fullWidth
                    />
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <TextField
                      required={modelField.required}
                      id={`${modelField.field}-porcentagem-${option.id}`}
                      label="Porcentagem da Doação"
                      type="number"
                      value={pessoaDoacaoValue.porcentagem_doacao || ''}
                      onChange={(e) => {
                        const newDoacaoValue = doacaoValue.filter(d => d.pessoa_inventario_id !== option.id);
                        newDoacaoValue.push({
                          pessoa_inventario_id: option.id,
                          porcentagem_doacao: parseInt(e.target.value)
                        });
                        handleChange(modelField.field, newDoacaoValue);
                      }}
                      fullWidth
                      inputProps={{
                        min: 0,
                        max: 100,
                        step: 0.01
                      }}
                    />
                  </Grid>
                </Fragment>
              );
            })}
          </Grid>
        );

      case 'internacionalAddress':
        const internationalAddressValue = value || {};
        return (
          <Grid container columnSpacing={2} marginBottom={0}>
            <Grid item xs={12} md={6}>
              <TextField
                required={modelField.required}
                id={`${modelField.field}-rua`}
                label="Rua"
                value={internationalAddressValue.rua || ''}
                onChange={(e) => handleChange(modelField.field, e.target.value, 'rua')}
                fullWidth
              />
              <TextField
                required={modelField.required}
                id={`${modelField.field}-cidade`}
                label="Cidade"
                value={internationalAddressValue.cidade || ''}
                onChange={(e) => handleChange(modelField.field, e.target.value, 'cidade')}
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                required={modelField.required}
                id={`${modelField.field}-estado`}
                label="Estado"
                value={internationalAddressValue.estado || ''}
                onChange={(e) => handleChange(modelField.field, e.target.value, 'estado')}
                fullWidth
              />
              <TextField
                required={modelField.required}
                id={`${modelField.field}-pais`}
                label="País"
                value={internationalAddressValue.pais || ''}
                onChange={(e) => handleChange(modelField.field, e.target.value, 'pais')}
                fullWidth
              />
            </Grid>
          </Grid>
        );  

      case 'separator':
        return (
          <Box sx={{ width: '100%', mt: 2, mb: 1 }}>
            {/* <Divider sx={{ mt: 1 }} /> */}
            <Typography variant="h5" gutterBottom>
              {modelField.label}
            </Typography>
            {modelField.description && (
              <Typography variant="body2" color="text.secondary">
                {modelField.description}
              </Typography>
            )}
          </Box>
        );

      default:
        return (
          <TextField
            required={modelField.required}
            id={modelField.field}
            label={modelField.label}
            value={value}
            onChange={(e) => handleChange(modelField.field, e.target.value)}
            fullWidth
          />
        );
    }
  };
  
  const evaluateVisibility = (modelField) => {
    let isVisible = true;
    if (typeof modelField.visible === 'boolean') {
      isVisible = modelField.visible;
    } else if (typeof modelField.visible === 'string') {
      const expression = modelField.visible;
      
      // Split by OR operator first
      const orParts = expression.split('||').map(part => part.trim());
      
      isVisible = orParts.some(orPart => {
        // Split by AND operator
        const andParts = orPart.split('&&').map(part => part.trim());
        
        return andParts.every(condition => {
          if (condition.includes('===')) {
            const [field, value] = condition.split('===').map(s => s.trim());
            return formData[field] === value.replace(/["']/g, '');
          } else if (condition.includes('!==')) {
            const [field, value] = condition.split('!==').map(s => s.trim());
            return formData[field] !== value.replace(/["']/g, '');
          }
          return true; // Invalid condition defaults to true
        });
      });
    }
    return { display: isVisible ? 'block' : 'none' };
  };
  
  return (
    <Fragment>
      <Dialog
        open={props.open}
        onClose={props.onClose}
        PaperProps={{
          component: 'form',
          onSubmit: handleSubmit,
        }}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle sx={{ mb: -1.5 }}>
          <h3>Edição de {title}</h3>
        </DialogTitle>
        <Divider />

        <DialogContent>
          <form>
            <Grid container columnSpacing={2}>
              {dataModel.map((modelField, idx) => (
                (
                  <Grid item md={modelField.sizeMD} sm={modelField.sizeSM} xs={modelField.sizeSM} key={idx}
                    sx={{
                      display: evaluateVisibility(modelField).display,
                    }}
                  >
                    <Box
                      sx={{
                        '& > *': { mb: 3 },
                        '& .MuiTextField-root': { width: '100%', mb: 2 },
                        '& .MuiFormControl-root': { mb: 2 },
                        '& .MuiFormControlLabel-root': { mb: 2 },
                        pt: 1,
                        display: evaluateVisibility(modelField).display,
                      }}
                    >
                      {renderField(modelField, dataRelationOptions)}
                    </Box>
                  </Grid>
                )
              ))}
            </Grid>
          </form>
        </DialogContent>

        <Divider />
        <DialogActions>
          <Button onClick={props.onClose}>Cancelar</Button>
          <Button variant="contained" type="submit">Salvar</Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
}